html, body, [canvas=container], [off-canvas] {    
    font-family: 'Open Sans', sans-serif !important;
    color:$default-text-color;
    letter-spacing: -0.3px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
}
.main {
    padding-top:0 !important;
    width:100% !important;
    float:none !important;
    background-color:white;
}

.color-logo {
    background-image: url('data:image/svg+xml;base64,');
    background-repeat: no-repeat;
}

// Custom Icons
@font-face {
    font-family: 'icomoon';
    src:    url('../fonts/icomoon.eot?52ck8n');
    src:    url('../fonts/icomoon.eot?52ck8n#iefix') format('embedded-opentype'),
        url('../fonts/icomoon.ttf?52ck8n') format('truetype'),
        url('../fonts/icomoon.woff?52ck8n') format('woff'),
        url('../fonts/icomoon.svg?52ck8n#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

