.overlay {
    position: relative;
    .outer {
        position: absolute;
        top:0;
        left:0;
        z-index: 2;
        height:100%;
        width:100%;
        display: table;
    }   
    .inner {
        display: table-cell;
        vertical-align: middle;
    }
}


