@media only screen and (min-width : 320px) {

    h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
        color: $black;
        margin-top:0;
        line-height: 1.2em;
        margin-bottom:30px;
    }
    h1 {
        font-weight: $font-semi-bold;
    }
    h2, h3 {
        font-weight: $font-light;
        line-height: 1.25em;
    }

    h4, h5, h6 {
        font-weight: $font-semi-bold;       
    }
    h1 {        
        letter-spacing:-2px;
        font-size: 36px;
    }
    h2 {        
        letter-spacing:-1px;
        font-size: 30px;
    }
    h3 {
        font-size: 24px;
    }
    h4 {
        font-size: 18px;
    }
    h5 {
        font-size: 14px;
    }
    p, li, .lead {      
        font-size:14px;
    }
    .lead, p, li {
        font-weight:$font-light;
        line-height: 1.5em;
    }
    .lead-lg {
        font-weight: $font-light;
        font-size:24px;
        color:$primary-alt-1;
    }
    ul, p, blockquote {
        margin-bottom:1.5em;
    }
    ul, ol {
        padding-left:20px;
    }
    a {
        color:$primary;
        cursor: pointer;
    }
    a.link-reverse {
        color:white;
        border-bottom:white dotted 1px;
        cursor: pointer;
    }
    a.link-reverse:hover {
        color:$primary-dark;
    }
    a.link-black {
        color:$black;
    }
    a:hover {
        color:$primary-dark;
        text-decoration:none;
    }
    hr {
        border-top: 5px solid $light-grey-4;
        margin:30px 0;
    }
    .background-clouds hr {
        border-top:darken($clouds, 5%) solid 5px
    }

    table {
        h4 {
            margin:20px 0; 
        }
        .thead-inverse {
            background-color:$concrete;
            border:none;
            th {
                color:white;
            }
        }
        tr>th, td, td p, th p, td li {
            font-size:14px;
            font-weight: $font-light;
        }
        tr>th, th p {
            font-weight: $font-normal;
        }

    }
    table.feature-table {
        border-collapse: separate;
        border-spacing: 10px 0px;
        th {
            text-transform: uppercase;
            font-weight: $font-semi-bold;
            color:$white;
            background-color:$secondary;
        }
        th:nth-child(1) {
            background:none;
        }
        tr:hover {
            background-color:$light-grey-4;
        }
        td {
            border-left:$light-grey-4 solid 1px;
            border-right:$light-grey-4 solid 1px;
            border-top:$light-grey-4 solid 1px !important;
        }
        thead tr:hover {
            background:none;
        }
        tbody tr:last-child td {
            border-bottom:$light-grey-4 solid 1px;
        }
        .fa-check {
            color:$success;
        }
    }
    table.feature-table-alt-1 {
        border-collapse: separate;
        border-spacing: 10px 0px;
        th {
            text-transform: uppercase;
            font-weight: $font-semi-bold;
            color:$white;
            background-color:$secondary;
        }
        tr:hover {
            background-color:$light-grey-4;
        }
        td {
            border-left:$light-grey-4 solid 1px;
            border-right:$light-grey-4 solid 1px;
            border-top:$light-grey-4 solid 1px !important;
        }
        thead tr:hover {
            background:none;
        }
        tbody tr:last-child td {
            border-bottom:$light-grey-4 solid 1px;
        }
    }
    blockquote {
        border-left:$peter-river solid 5px;
        background:$clouds;
        padding:30px;
        margin-bottom:1.63em;
        li p:last-child {
            margin-bottom:1.63em;
        }
    }
    figure.wp-caption {
        background-color:$clouds !important;
        padding:15px !important;
        margin-bottom:1.63em !important;
        border-radius:0 !important;
        figcaption {
            padding:15px;
        }
    }
    figure[style] {
        max-width:100% !important;
    }

    .section-full {
        padding:60px 0;
         @include media(">=tablet") {
            height:100vh;
            .middle {                
                position: absolute;
                top:50%;
                left: 0%;
                transform:translate(0%, -50%);
                -webkit-transform:translate(0%, -50%);
                width:100%;
            }
        }
    }

    .section-padding {
        padding:50px 0;
    }
    .page-padding {
        padding-top:90px;
        padding-bottom:60px;
    }
    .padding-15 {
        padding:15px;
    }
    .padding-30 {
        padding:30px;
    }
    .padding-60 {
        padding:60px;
    }
