                color:$midnight-blue;
            }
            a:hover {
                text-decoration: underline;
            }
        }
        .event-categories {
            display: block;
            margin-bottom:15px;
            a {
                background-color:$silver;
                font-size:11px;
                text-transform: uppercase;
                color:white;
                padding:5px;
            }
            a:hover {
                text-decoration: none;
            }
        }
    }


/* centered columns styles */
.row-centered {
    text-align:center;
}

[class*="col-centered"] {
    display: inline-block;
    float:none;
    /* reset the text-align */
    text-align:left;
    /* inline-block space fix */
    margin-right:-4px;
    vertical-align: top;
}

/* Default BS breakpoints */
@media (min-width: 768px) {
  .col-centered-sm {
    display: inline-block
  }
}

@media (min-width: 992px) {
  .col-centered-md {
    display: inline-block
  }
}

@media (min-width: 1200px) {
  .col-centered-lg {
    display: inline-block
  }
}

.thumbnail {
    padding: 0;
    border-radius: 0;
    border:none;
    background:none;
    .caption {
        padding:15px 0;
    }
}

//Search 
.search-result {
    display: block;
    margin-bottom:30px;
    h4 {
        font-weight: $font-normal;
    }
    .searchwp-highlight {
        font-weight: $font-bold;
    }
    p {
        margin:0;
        line-height: normal;
    }
}
.main .search-form {
    position: relative;
    height: 50px;
    margin-bottom:30px;
    input.search-field {
        border:$grey solid 1px;
        position: absolute;
        top:0;
        left:0;
        width:100%;
        height:50px;
        font-size: 14px;
        padding-left:20px;
        padding-right:120px;
        font-weight: $font-normal;
        z-index: 1;


    }
    input.search-submit {
        background:$primary;
        border:none;
        text-transform: uppercase;
        font-size: 14px;
        padding-left:20px;        
        padding-right:20px;
        color:white;
        font-weight: $font-semi-bold;
        position: absolute;
        top:0;
        right:0;
        z-index: 2;
        height: 50px;
    }

}

.modal-backdrop {
    display:none;
}
.modal.video-modal {
    position: absolute !important;
    top:0 !important;
    left:0 !important;
    bottom:0 !important;
    right:0 !important;
    z-index: 2000 !important;
    height:100vh !important;
    width:100% !important;
    .modal-dialog {
        margin:0;
        padding:0;
        width:100%;
        height:100vh;
        background:$white;
    }
    .modal-content {
        position: relative;
        width:100%;
        height:100vh;
        display: table;
        box-shadow:none;
        border:none;
        background:transparent;
        padding-top:60px;
        .modal-body {
            display:table-cell;
            vertical-align:middle;
        }        
        button {
            position: absolute;
            top:90px;
            right:30px;
            height:60px;
            width:60px;
            z-index: 10;
            color:$black;
            background-color: $white;
            border: none;
            font-size: 50px;
            padding:0;
            outline:none;
            cursor:pointer;
            line-height: normal;
        }

    }

}
.modal.video-modal[style] {
    padding:0 !important;
}