.home-masthead {
	.slide, .single-slide {
		width:100%;
		background-repeat:no-repeat;
		background-position:center center;
		position: relative;
		background-size: cover;
		.text {
			position: absolute;
			top:0;
			left:0;
			width:100%;
			height:100%;
			z-index: 100;
			.btn { 
				border:none;
			}
		}
	}
	.slide:after, .single-slide:after {
		content: '';
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		position: absolute;
		z-index: 3;
		background: rgba($primary-dark, 0.3);
	}
}

.masthead, .single-masthead {
	position: relative;	
	.text {
		position: absolute;
		z-index: 10;
		bottom:60px;
		left:0;
		width:100%;
		h3 {
			font-weight: $font-normal;
		}
	}
	.img {
		position: absolute;
		top:0;
		left:0;
		width:100%;
		height:100%;
		filter: contrast(1.1) saturate(1.9) grayscale(0.3);
	}
}
.masthead:before, .masthead:after, .single-masthead:before, .single-masthead:after {
	content: '';
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	position: absolute;
}
.masthead:before {
	z-index: 2;
	mix-blend-mode: multiply;
	box-shadow: 0 0 250px $black inset;
	background: rgba($primary-dark, 0.3);
} 
.masthead:after, .single-masthead:after {
	z-index: 3;
	background: rgba($primary-dark, 0.3);
}

@media only screen and (min-width : 320px) {	
	.masthead .text {
		bottom:50px;
	}	
	.single-masthead {
		height:200px;
	}
}

@media only screen and (min-width : 992px) {
	.masthead .text {
		bottom:60px;
	}
}

@media screen and ( min-height: 300px ){
    .home-masthead, .home-masthead .slide, .home-masthead .single-slide {
    	height:400px;
    }
    .masthead {
		height:300px;
	}
}

@media screen and ( min-height: 768px ){
    .home-masthead, .home-masthead .slide, .home-masthead .single-slide {
    	height:600px;
    }
    .masthead {
		height:400px;
	}
}

@media screen and ( min-height: 1080px ){
    .home-masthead, .home-masthead .slide, .home-masthead .single-slide {
    	height:720px;
    }
    .masthead {
		height:500px;
	}
}


