			-o-transition: all 8s ease;
		}
		.animate {
			transform: scale(1.1);
			-moz-transform: scale(1.1);
			-webkit-transform: scale(1.1);
			-o-transform: scale(1.1);
		}
	}
	.vertical-center{
	    position: absolute;
	    top:50%;
	    left: 0%;
	    transform:translate(0%, -50%);
	    -webkit-transform:translate(0%, -50%);
	    width:100%;
	}
    .scroll {
    	position: absolute;
    	width:100%;
    	bottom:0;
    	left:0;
    	z-index: 2;
    	padding:15px 0;
    	.mouse {
        	position: relative;
		    margin:0 auto;
		    height: 45px;
		    width: 25px;
		    background-image: url("../images/mouse.svg");
		    background-repeat: no-repeat;
		    background-size: contain;
		    -webkit-transition: -webkit-transform .3s .9s ease-out,opacity .3s .9s ease-out;
		    transition: transform .3s .9s ease-out,opacity .3s .9s ease-out;
		    a {
		    	position: absolute;
		    	width:100%;
		    	height:100%;
		    	top:0;
		    	left:0;
		    }
    	}
    	.mouse:after {
			position: absolute;
			top: 10px;
			left: 50%;
			margin-left: -3px;
			border-radius: 10px;
			content: '';
			width: 6px;
			height: 6px;
			background-color: $black;
			-webkit-animation-name: mouseScroll;
			animation-name: mouseScroll;
			-webkit-animation-duration: 2s;
			animation-duration: 2s;
			-webkit-animation-iteration-count: infinite;
			animation-iteration-count: infinite;
    	}
		@-webkit-keyframes mouseScroll {
			0%, 50% {
				opacity: 1;
				-webkit-transform: translateY(0);
				transform: translateY(0)
			}
			90% {
				opacity: 0;
				-webkit-transform: translateY(20px);
				transform: translateY(20px)
			}
			91% {
				opacity: 0;
				-webkit-transform: translateY(0);
				transform: translateY(0)
			}
		}
		@keyframes mouseScroll {
			0%, 50% {
				opacity: 1;
				-webkit-transform: translateY(0);
				transform: translateY(0)
			}
			90% {
				opacity: 0;
				-webkit-transform: translateY(20px);
				transform: translateY(20px)
			}
			91% {
				opacity: 0;
				-webkit-transform: translateY(0);
				transform: translateY(0)
			}
		}
    }
