/* Timeline */
@media only screen and (min-width : 320px) {
	.timeline-container {
		width: 90%;
		margin: 15px auto 15px auto;
		position: relative;
		padding: 2em 0;
		margin-top: 1em;
		margin-bottom: 1em;
	}
	.timeline-container::after {
		content: '';
		position: absolute;
		bottom: -12px;
		left: 10px;
		height: 0;
		width: 0;
		border: 10px solid transparent;
		border-top: 10px solid $light-grey-1;
	}
	.timeline-container::before {
	  /* this is the vertical line */
		content: '';
		position: absolute;
		top: 0;
		left: 18px;
		height: 100%;
		width: 5px;
		background: $light-grey-1;
	}

	.timeline-btn-wrap {
		width: 90%;
		margin: 0 auto;
		position: relative;
		height:27px;
		.timeline-btn {
		    border-radius: 1px;
		    background-color: $light-grey-4;
		    text-transform: uppercase;
		    color: $light-grey-1;
		    padding: 5px 15px;
		    display: inline-block;
		    font-size: 12px;
		    .now {
			   position: absolute;
			    top: 0;
			    left: 0;
			}
			.launch {
			    position: absolute;
			    bottom: 0;
			    left: 0;
			}
		}
	}

	.timeline-block {
		position: relative;
		margin: 2em 0;
	}
	.timeline-block:after {
		content: "";
		display: table;
		clear: both;
	}
	.timeline-dot {
		position: absolute;
		top: 14px;
		left: 10px;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		border: 4px solid white;
		background: $light-grey-1;
	}
	.timeline-image {
		position: absolute;
		top: 0;
		left: -5px;
		border: 4px solid #fff;
		width: 50px;
		height: 50px;
	}
	.timeline-content {
		position: relative;
		margin-left: 60px;
		background: $primary-alt-2-light;
		border-radius: 1px;
		padding: 15px;
		h4 {
			margin:0;
		}
	}
	.timeline-content:after {
		content: "";
		display: table;
		clear: both;
	}
	.timeline-content::before {
		content: '';
		position: absolute;
		top: 16px;
		right: 100%;
		height: 0;
		width: 0;
		border: 10px solid transparent;
		border-right: 10px solid $light-grey-4;
	}
}

@media only screen and (min-width : 992px) {
	.timeline-container::after {
		left: 50%;
		margin-left: -10px;
	}
	.timeline-container::before {
		left: 50%;
		margin-left: -2px;
	}
	.timeline-btn-wrap {
		padding-top: 0;
		text-align: center;
	}
	.timeline-dot {
		width: 20px;
		height: 20px;
		left: 50%;
		top: 23px;
		margin-left: -10px;
		/* Force Hardware Acceleration in WebKit */
		-webkit-transform: translateZ(0);
		-webkit-backface-visibility: hidden;
	}
	.timeline-image {
		width: 70px;
		height: 70px;
		left: 50%;
		top: 0px;
		margin-left: -35px;
		/* Force Hardware Acceleration in WebKit */
		-webkit-transform: translateZ(0);
		-webkit-backface-visibility: hidden;
	}
	.timeline-content {
		margin-left: 0;
		padding: 1.6em;
		width: 45%;
	}
	.timeline-content::before {
		top: 24px;
		left: 100%;
		border-color: transparent;
		border-left-color: $primary-alt-2-light;
	}
	.timeline-block:nth-child(even) .timeline-content {
		float: right;
		text-align: left;
	}
	.timeline-block:nth-child(even) .timeline-content::before {
		top: 24px;
		left: auto;
		right: 100%;
		border-color: transparent;
		border-right-color: $primary-alt-2-light;
	}
	.timeline-block:nth-child(odd) .timeline-content {
		text-align: right;
	}
	.cssanimations {
		.timeline-dot.is-hidden, .timeline-image.is-hidden {
			visibility: hidden !important;
		}
		.timeline-dot.bounce-in, .timeline-image.bounce-in {
			visibility: visible;
			-webkit-animation: socrata-bounce-1 0.6s;
			-moz-animation: socrata-bounce-1 0.6s;
			animation: socrata-bounce-1 0.6s;
		}
		.timeline-content.is-hidden {
			visibility: hidden;
		}
		.timeline-content.bounce-in {
			visibility: visible;
			-webkit-animation: socrata-bounce-2 0.6s;
			-moz-animation: socrata-bounce-2 0.6s;
			animation: socrata-bounce-2 0.6s;
		}
		.timeline-block:nth-child(even) .timeline-content.bounce-in {
			-webkit-animation: socrata-bounce-2-inverse 0.6s;
			-moz-animation: socrata-bounce-2-inverse 0.6s;
			animation: socrata-bounce-2-inverse 0.6s;
		}
	}
}

@-webkit-keyframes socrata-bounce-1 {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
  }
  60% {
    opacity: 1;
    -webkit-transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
  }
}

@-moz-keyframes socrata-bounce-1 {
  0% {
    opacity: 0;
    -moz-transform: scale(0.5);
  }
  60% {
    opacity: 1;
    -moz-transform: scale(1.2);
  }
  100% {
    -moz-transform: scale(1);
  }
}
@keyframes socrata-bounce-1 {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
    -moz-transform: scale(0.5);
    -ms-transform: scale(0.5);
    -o-transform: scale(0.5);
    transform: scale(0.5);
  }
  60% {
    opacity: 1;
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
}
@-webkit-keyframes socrata-bounce-2 {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-100px);
  }

  60% {
    opacity: 1;
    -webkit-transform: translateX(20px);
  }

  100% {
    -webkit-transform: translateX(0);
  }
}
@-moz-keyframes socrata-bounce-2 {
  0% {
    opacity: 0;
    -moz-transform: translateX(-100px);
  }

  60% {
    opacity: 1;
    -moz-transform: translateX(20px);
  }

  100% {
    -moz-transform: translateX(0);
  }
}

@keyframes socrata-bounce-2 {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-100px);
    -moz-transform: translateX(-100px);
    -ms-transform: translateX(-100px);
    -o-transform: translateX(-100px);
    transform: translateX(-100px);
  }

  60% {
    opacity: 1;
    -webkit-transform: translateX(20px);
    -moz-transform: translateX(20px);
    -ms-transform: translateX(20px);
    -o-transform: translateX(20px);
    transform: translateX(20px);
  }

  100% {
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0);
  }
}

@-webkit-keyframes socrata-bounce-2-inverse {
  0% {
    opacity: 0;
    -webkit-transform: translateX(100px);
  }

  60% {
    opacity: 1;
    -webkit-transform: translateX(-20px);
  }

  100% {
    -webkit-transform: translateX(0);
  }
}
@-moz-keyframes socrata-bounce-2-inverse {
  0% {
    opacity: 0;
    -moz-transform: translateX(100px);
  }

  60% {
    opacity: 1;
    -moz-transform: translateX(-20px);
  }

  100% {
    -moz-transform: translateX(0);
  }
}
@keyframes socrata-bounce-2-inverse {
  0% {
    opacity: 0;
    -webkit-transform: translateX(100px);
    -moz-transform: translateX(100px);
    -ms-transform: translateX(100px);
    -o-transform: translateX(100px);
    transform: translateX(100px);
  }
  60% {
    opacity: 1;
    -webkit-transform: translateX(-20px);
    -moz-transform: translateX(-20px);
    -ms-transform: translateX(-20px);
    -o-transform: translateX(-20px);
    transform: translateX(-20px);
  }
  100% {
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0);
  }
}

.sixteen-nine {
  @include aspect-ratio(16, 9);
}

